
.slide-container {
    position: relative;
    overflow: hidden;
}


.slide {
    width: 100%; /* 设置容器宽度为100% */
    height: 100vh; /* 设置幻灯片容器的高度，可以根据需要调整 */
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
    transition: opacity 1s ease-in-out;
}



.slide.visible {
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
}

.heroimg {
    width: 200vh;
    height: 60vh;
}

.prev {
    position: absolute;
    top: 50%;
    width: auto;
    padding: 16px;
    margin-top: -22px;
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
    left: 0;
}
.next {
    position: absolute;
    top: 50%;
    width: auto;
    padding: 16px;
    margin-top: -22px;
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
    right: 0;

}
